<template>
  <div class="history">
    <div class="nav">
      <div class="back">
        <i
          class="fa fa-angle-left"
          aria-hidden="true"
          @click="$router.go(-1)"
        ></i>
      </div>
      <div class="title">
        <span>播放历史</span>
      </div>
      <div class="title" @click="clearHistory">
        <span>清除</span>
      </div>
    </div>
    <div class="content" v-if="playhistorydata">
      <ul v-for="(item, index) in playhistorydata" :key="index">
        <li @click="gocontenttody(item)">
          <div class="img">
            <img :src="item.image" alt="" />
            <h5 class="time">{{ getmin(item.duration) }}</h5>
          </div>
          <div class="content">
            <h2 class="title">{{ item.title }}</h2>
            <div class="bottom">
              <van-icon name="star" />
              <van-icon name="star" />
              <van-icon name="star" />
              <van-icon name="star" />
              <van-icon name="star" />
              <span class="grade">{{ item.rating }}分</span>
              <span class="submit"
                ><van-icon name="upgrade" />{{ item.share_num }}</span
              >
            </div>
          </div>
        </li>
      </ul>
      <div class="end">-End-</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      playhistorydata: null
    };
  },
  methods: {
    //清除观看历史
    clearHistory() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      window.localStorage.setItem(
        "user",
        JSON.stringify({
          tel: user.tel,
          password: user.password,
          data: {
            like: user.data.like,
            playhistory: []
          }
        })
      );
      this.playhistorydata = [];
    },
    gocontenttody(item) {
      this.$store.commit("submitIsactive", true);
      this.$store.commit("submitLikeItem", item);
      this.$router.push({
        path: "finditemcontent",
        query: { id: item.postid }
      });
    },
    getmin(i) {
      let time = i;
      let n = Math.floor(time / 60);
      let s = Math.floor(time % 60);
      if (n < 10) {
        n = "0" + n;
      }
      if (s < 10) {
        // window.console.log("0" + s);
        s = "0" + s;
      }
      let m = n + "'" + s + '"';

      return m;
    },
    //拿到收藏的数据
    geiHistory() {
      let user = JSON.parse(window.localStorage.getItem("user"));
      this.playhistorydata = user.data.playhistory;
      window.console.log(user.data.playhistory);
    }
  },
  created() {
    this.geiHistory();
  }
};
</script>

<style lang="less">
.history {
  height: 100vh;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  .nav {
    height: 10vh;
    display: flex;
    color: rgb(197, 195, 195);
    background: black;
    align-items: center;
    .title {
      width: 50%;
      text-align: center;
    }
    .actives {
      color: white;
      span {
        padding: 23px 5px;
        border-bottom: 2px solid red;
      }
    }
    .back {
      flex: 1;
      i {
        font-size: 1.5em;
        padding-left: 15px;
      }
    }
  }
  .content {
    background: rgb(247, 244, 244);
    flex: 1;
    overflow-y: scroll;
    ul {
      background: white;
      li {
        background: white;
        display: flex;
        padding: 10px 10px 10px 10px;
        .img {
          width: 30%;
          position: relative;
          .time {
            position: absolute;
            right: 5px;
            bottom: 5px;
            color: white;
            font-size: 10px;
          }
        }
        .content {
          background: white;
          padding: 0px 15px;
          .title {
            padding: 5px 0;
            text-align: left;
            font-size: 13px;
          }
          .bottom {
            .van-icon {
              font-size: 10px;
            }
            .grade {
              font-size: 12px;
              padding: 0px 5px;
            }
            .submit {
              font-size: 12px;
              padding: 0px 5px;
              .van-icon {
                padding: 0px 3px;
                line-height: 16px;
                transform: translateY(1px);
              }
            }
          }
        }
      }
    }
    .end {
      text-align: center;
      padding: 10px 0;
      color: rgb(97, 96, 96);
    }
  }
}
</style>
